/* src/App.css */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 600px;
  margin: auto;
  padding: 20px;
}

.heading {
  color: #333;
}

.textarea {
  width: 100%;
  height: 150px;
  resize: vertical; /* Allow vertical resizing */
  margin-top: 10px;
}

.word-count {
  margin-top: 10px;
  color: #555;
}
